or: #2f557b;\r\n      color: white;\r\n      border: none;\r\n      border-radius: 4px;\r\n      cursor: pointer;\r\n      transition: background-color 0.3s;\r\n      align-self: center;\r\n      &:hover {\r\n        background-color: #bf1722;\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
